body {
  background-color: #d0e6f9;
}

.bounce-in-bottom {
  animation: bounce-in-bottom 1.1s both;
}

@keyframes bounce-in-bottom {
  0% {
    opacity: 0;
    animation-timing-function: ease-in;
    transform: translateY(500px);
  }

  38% {
    opacity: 1;
    animation-timing-function: ease-out;
    transform: translateY(0);
  }

  55% {
    animation-timing-function: ease-in;
    transform: translateY(65px);
  }

  72% {
    animation-timing-function: ease-out;
    transform: translateY(0);
  }

  81% {
    animation-timing-function: ease-in;
    transform: translateY(28px);
  }

  90% {
    animation-timing-function: ease-out;
    transform: translateY(0);
  }

  95% {
    animation-timing-function: ease-in;
    transform: translateY(8px);
  }

  100% {
    animation-timing-function: ease-out;
    transform: translateY(0);
  }
}

.button {
  cursor: pointer;
  width: 100px;
  height: 35px;
  color: #fff;
  background-color: #8d7df5;
  border: none;
  border-radius: 25px;
  margin-right: 15px;
  font-size: 17px;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.button:hover {
  color: #000;
  background-color: #7c6af4;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

input {
  width: 250px;
  height: 35px;
  border: none;
  border-radius: 25px;
  padding-left: 10px;
  font-size: 17px;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

#search-form {
  justify-content: center;
  align-items: center;
  gap: 5px;
  margin: 20px;
  display: flex;
}

.load-more {
  width: 200px;
  height: 50px;
  color: #fff;
  cursor: pointer;
  background-color: #00a0d2;
  border: none;
  border-radius: 10px;
  margin: 5px;
  font-size: 20px;
}

.load-more:hover {
  color: #000;
  background-color: #0cb4e7;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.gallery {
  grid-gap: 20px;
  max-width: calc(100vw - 80px);
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  margin: 0 auto 30px;
  display: grid;
}

.photo-card {
  border-radius: 10px;
  position: relative;
  overflow: hidden;
}

.photo-card img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform .3s;
}

.info {
  background-color: rgba (0, 0, 0, .5);
  color: #fff;
  opacity: 0;
  justify-content: space-between;
  padding: 10px;
  transition: opacity .3s;
  display: flex;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.info-item {
  margin: 0;
}

.photo-card:hover img {
  transform: scale (1.1);
}

.photo-card:hover .info {
  opacity: 1;
}

